// log
import store from "../store";

const fetchDataRequest = () => {
  return {
    type: "CHECK_DATA_REQUEST",
  };
};

const fetchDataSuccess = (payload) => {
  return {
    type: "CHECK_DATA_SUCCESS",
    payload: payload,
  };
};

const fetchDataFailed = (payload) => {
  return {
    type: "CHECK_DATA_FAILED",
    payload: payload,
  };
};

// function sleep(ms) {
//   return new Promise(resolve => setTimeout(resolve, ms));
// }



export const fetchData = (account, wlLevel) => {
  return async (dispatch) => {
    dispatch(fetchDataRequest());
    try {
      let paused = await store
        .getState()
        .blockchain.smartContract.methods.paused()
        .call();
      let whitelistIsOn = await store
        .getState()
        .blockchain.smartContract.methods.whitelistMintEnabled()
        .call();
      console.log("whitelistIsOn:", whitelistIsOn);

      let totalSupply = await store
        .getState()
        .blockchain.smartContract.methods.totalSupply()
        .call();
      let maxSupply = 4888;

      let batchSupply = await store
        .getState()
        .blockchain.smartContract.methods.batchSupply()
        .call();
      batchSupply = batchSupply - 1;

      let cost = await store
        .getState()
        .blockchain.smartContract.methods.cost()
        .call();

      let wlCost = await store
        .getState()
        .blockchain.smartContract.methods.wlCost()
        .call();
      let superWlCost = await store
        .getState()
        .blockchain.smartContract.methods.superWlCost()
        .call();
      let alphaWlCost = await store
        .getState()
        .blockchain.smartContract.methods.alphaWlCost()
        .call();
      let maxMintAmountPerTx = await store
        .getState()
        .blockchain.smartContract.methods.maxMintAmountPerTx()
        .call();
      maxMintAmountPerTx = maxMintAmountPerTx - 1;

      let superWLMaxMintAmountPerTx = await store
        .getState()
        .blockchain.smartContract.methods.superWLMaxMintAmountPerTx()
        .call();
      superWLMaxMintAmountPerTx = superWLMaxMintAmountPerTx - 1;
      let alphaWlMaxMintAmountPerTx = await store
        .getState()
        .blockchain.smartContract.methods.alphaWlMaxMintAmountPerTx()
        .call();
      alphaWlMaxMintAmountPerTx = alphaWlMaxMintAmountPerTx - 1;
      let batchSaleIsOn = await store
        .getState()
        .blockchain.smartContract.methods.batchSaleEnabled()
        .call();
      let maxMintAmountPerWl = await store
        .getState()
        .blockchain.smartContract.methods.maxMintAmountPerWl()
        .call();
      maxMintAmountPerWl = maxMintAmountPerWl - 1;

      let maxMintAmountPerSuperWl = await store
        .getState()
        .blockchain.smartContract.methods.maxMintAmountPerSuperWl()
        .call();
      maxMintAmountPerSuperWl = maxMintAmountPerSuperWl - 1;
      let maxMintAmountPerAlphaWl = await store
        .getState()
        .blockchain.smartContract.methods.maxMintAmountPerAlphaWl()
        .call();
      maxMintAmountPerAlphaWl = maxMintAmountPerAlphaWl - 1;

      let batch = await store
        .getState()
        .blockchain.smartContract.methods.batch()
        .call();
      console.log("Batch" + batch)
      let batchMintedForBuyer = await store
        .getState()
        .blockchain.smartContract.methods.batchMintedForBuyer(account, wlLevel)
        .call();
      let batchMinted = await store
        .getState()
        .blockchain.smartContract.methods.batchMinted()
        .call();
      console.log("BatchMinted" + batchMinted)
      console.log({
        totalSupply,
        cost,
        whitelistIsOn,
        paused,
        maxSupply,
        batch,
        batchSupply,
        batchMinted,
        batchMintedForBuyer,
        wlCost,
        superWlCost,
        alphaWlCost,
        maxMintAmountPerTx,
        superWLMaxMintAmountPerTx,
        alphaWlMaxMintAmountPerTx,
        batchSaleIsOn,
        maxMintAmountPerWl,
        maxMintAmountPerSuperWl,
        maxMintAmountPerAlphaWl
      });
      dispatch(
        fetchDataSuccess({
          totalSupply,
          cost,
          whitelistIsOn,
          paused,
          maxSupply,
          batch,
          batchSupply,
          batchMinted,
          batchMintedForBuyer,
          wlCost,
          superWlCost,
          alphaWlCost,
          maxMintAmountPerTx,
          superWLMaxMintAmountPerTx,
          alphaWlMaxMintAmountPerTx,
          batchSaleIsOn,
          maxMintAmountPerWl,
          maxMintAmountPerSuperWl,
          maxMintAmountPerAlphaWl
        })
      );
    } catch (err) {
      console.log(err);
      dispatch(fetchDataFailed("Could not load data from contract."));
    }
  };
};